#commercecsvimportform {
  fieldset {
    padding: 15px;
    margin: 0 0 20px 0;
    border: 2px solid rgba(black, 0.1);
    border-radius: 4px;
    background-color: white;
    fieldset {
      margin: 34px;
      &:last-of-type{
        margin-bottom: 34px;
      }
      legend {
        font-size: 20px;
      }
    }
    legend {
      font-size: 24px;
    }
  }

  button, input[type="submit"], .button {
    @extend %button;
  }

  .form-type-checkbox {
    position: relative;
    margin: 0;
    input {
      display: none;
    }
    label {
      margin: 8px 0 8px 16px;
      display: inline-block;
      cursor: pointer;
      padding-left: 16px;
      &:before {
        position: absolute;
        content: "";
        display: block;
        transition: 0.4s ease;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 18px;
        height: 18px;
        background-position: center;
        background-repeat: no-repeat;
        border: 2px solid rgba($color-gray, 0.33);
      }
      &:hover {
        &:before {
          border-color: $color-primary;
        }
      }
    }
  }

  //== checkbox
  .form-type-checkbox {
    label {
      &:before {
        border-radius: 3px;
      }
    }
    input[type="checkbox"]:checked + label {
      &:before {
        background-color: $color-primary;
        border-color: $color-primary;
        background-image: url('../images/check-white.svg');
      }
    }
  }


  //== form elements
  input,
  textarea,
  select {
    width: 230px;
    max-width: 100%;
    box-sizing: border-box;
    background: white;
    font-family: inherit;
    font-size: inherit;
    padding: 16px;
    box-shadow: inset 0 0 7px 1px rgba(234, 234, 234, .5);
    border: 1px solid darken(#F0F0F0, 10%);
    border-radius: 4px;
    margin-bottom: 15px;
    &:focus {
      background-color: $color-form-bg-hover;
      border-color: $color-form-border-hover;
      outline: none;
    }
  }
  input[type="text"],
  select {
    height: 44px;
    padding: 10px;
  }
  select {
    cursor: pointer;
    option {
      cursor: pointer;
    }
  }
  input:disabled {
    background-color: #eee;
  }
}