@import "components/colors";
@import "components/buttons";
@import "elements/html-elements";


//== checkbox ,radio base
#commercecsvimportform {

  [data-drupal-selector="edit-importing-rules"] {
    border: none;
  }

  [id="edit-import"] {
    @extend %submit-button;
  }
}

#log-section {
  margin-top: 30px;
  text-align: left;
  fieldset {
    margin-bottom: 80px;
    legend {
      top: -15px;
      padding: 5px 10px;
      font-weight: normal;
      color: black;
      background-color: white;
      text-transform: none;
    }
  }
  .log-section__title {
    font-weight: normal;
    font-size: 32px;
    margin: 60px 0 80px 0;
  }
  .log {
    margin-bottom: 40px;
    &__message {
    }
    &__label {
      font-weight: bold;
      padding-left: 34px;
      background: left 5px center/ auto no-repeat;
      &.notice {
        color: $color-notice;
        background-image: url('../images/notice.svg');
      }
      &.error {
        color: $color-error;
        background-image: url('../images/error.svg');
      }
    }
  }
  .overrider {
    padding-left: 34px;
    &-label {
      font-weight: normal;
      margin-bottom: 5px;
    }
    &-item {
      position: relative;
      display: inline-block;
      margin-right: 10px;
      input {
        padding-right: 28px;
      }
    }
    .override-status {
      position: absolute;
      width: 14px;
      height: 14px;
      right: 10px;
      top: 52px;
      background:  center/auto no-repeat;
      box-shadow: none;
      &.correct {
        background-image: url('../images/check.svg');
      }
      &.incorrect {
        background-image: url('../images/error.svg');
      }
    }
    .override-button {
      @extend %submit-button-second;
    }
  }
}



