%button {
  display: inline-block;
  border-radius: 4px;
  text-align: center;
  font-weight: 700;
  transition: .4s ease;
  width: auto;
  &:hover,
  &:active,
  &:focus {
    cursor: pointer;
    text-decoration: none;
  }
}

%submit-button {
  padding: 16px 50px;
  background-color: $color-primary;
  background-image: none;
  border: 0;
  border-bottom: 3px solid darken($color-primary, 5%);
  color: white;
  &:active {
    box-shadow: none;
  }
}

%submit-button-second {
  padding: 5px 15px;
  background-color: white;
  border: 2px solid $color-primary;
  color: $color-primary;
  font-weight: normal;
  &:hover {
    background-color: $color-primary;
    color: white;
  }
}